body{
    display: flex;
    justify-content: center;
    color: white;
}
.container{
    width: 414px;
    height: 660px;
    background-color: black;
    border-radius: 20px;
    margin-top: 20px;
}

.header{
    display: flex;
    justify-content: space-between;
    margin: 10px 15px auto 15px;
    color: rgb(0, 102, 255);
    font-size: 18px;
}

.header i{
    margin-top: 15px;
}

.header span{
    margin-top: 10px;
}

.contacts {
    margin-top: 10px;
    margin-left: 14px;
    text-align: start;
    font-size: 35px;
    margin-bottom: 10px;
}
.search {
    margin-left: 14px;
    display: flex;
    justify-content: flex-start;
    
}

.input{
    width: 85%;
    height: 28px;
    font-size: 16px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-left: transparent;
    border: 1px black;
    background-color: rgba(90, 90, 90, 0.664);
    color:white;
}
.search_icon{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border-right: transparent;
    border: 1px black;
    background-color: rgba(90, 90, 90, 0.664);
    color: rgb(129, 123, 123);
}

.profile{
    display: flex;
    margin: 10px 0 0 14px;
    border-bottom: 1px solid grey;
    border-top: 1px solid grey;
    width: 85%;
    padding: 10px 0 10px 0;
}

.profile img{
    height: 60px;
}

.profile div{
    margin-left: 15px;
    text-align: start;
}

.profile h1{
    font-size: 20px;
    margin: 0;
    margin-top: 8px;
}

.profile p{
    margin: 0;
    font-size: 14px;
    font-weight: 400;
    color:rgb(177, 166, 166);

}

.elements{
    height: 270px;
    overflow: auto;
}

.footer{
    margin-top: 17%;
}
.filter{
    display: flex;
    margin:20px 50px 0 30px ;
    justify-content: space-between;
}

.checkbox {
    display: flex;
}
.checkbox i{
    font-size: 18px;
}
.checkbox input{
    margin-left: 5px;
    font-size: 18px;
}
.green{
    color: greenyellow
}
.blue{
    color: rgb(0, 132, 255)
}
.pink{
    color: rgb(255, 47, 245)
}
